import React, { Component } from 'react';
import Cardlist from './Cardlist';
import Search from './Search';
import {connect} from 'react-redux';
import Header from './Header';
import {setSearchField,requestRobots} from "../actions";

const initMapStateToProps=(state)=>{
    const requestRobots=state.requestRobots;
    return {
        searchField:state.searchRobots.searchField,
        robots:requestRobots.robots,
        isPending:requestRobots.isPending,
        error:requestRobots.error,}
}
const initMapDispatchToProps=(dispatch)=>{
    return{
        onSearchChange:(event)=>dispatch(setSearchField(event.target.value)),
        onRequestRobots:()=>dispatch(requestRobots())}
}
class App extends Component {
    componentDidMount(){
        this.props.onRequestRobots();
    }

  render() {
        const {searchField,robots,isPending}=this.props;
        const filterRobots=robots.filter((robots)=>{
            return robots.name.toLowerCase().includes(searchField.toLowerCase());
        });

    return isPending ? <h1>Loding</h1>:(
      <div className="App">
           <Header/>
            <Search onSearchChange={this.props.onSearchChange}/>
            <Cardlist robots={filterRobots}/>
      </div>
    );
  }
}

export default connect(initMapStateToProps,initMapDispatchToProps)(App);



