import React from 'react';
import Card from './Card';
const Cardlist = ({robots}) => {
    const component=robots.map((user,i)=>{
        return <Card key={i} id={robots[i].id} email={robots[i].email} name={robots[i].name}/>
    });
    return (
        <div className="row">
            {component}
        </div>
    );

}


export default Cardlist;


