import React from 'react';const Card = ({id,name,email}) => {return (<div className="col-md-3 col-sm-4 col-xs-6"><div className="Card"><img src={`https://robohash.org/${id}?200x200`} alt=""/><h1>{name}</h1><p>{email}</p></div></div>);}
export default Card;


